/* reset */
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

/* 폰트 */
* {
  font-family: "Noto Sans KR", sans-serif;
}
.App {
  text-align: center;
}

.logo {
  margin: 50px auto;
  /* text-align: center; */
}
.logo img {
  width: 320px;
  cursor: pointer;
}

.main {
  width: 100%;
  height: 90vh;
  background: url("./main.jpg") no-repeat;
  background-size: cover;
  margin-bottom: 50px;
}

.product {
  width: 350px;
  margin-bottom: 10px;
}

/* 상품 한줄에 네개 */
.product2 {
  width: 300px;
  margin-bottom: 10px;
}

.con1 {
  margin: 150px auto 100px;
}

.con1 > h2 {
  font-weight: normal;
}

/* product name */
.con1 .title {
  font-size: 18px;
  height: 23px;
}

.con1 h2 {
  text-align: center;
  margin-bottom: 30px;
}
.con1 h4 {
  text-align: center;
  margin: 10px;
}

.con1 p:nth-of-type(1) {
  font-size: 14px;
}

.con1 p {
  text-align: center;
  margin: 3px;
}

.row {
  margin-bottom: 40px;
}

.con2 h2 {
  text-align: center;
  margin-bottom: 20px;
}

.con2 .title {
  font-size: 18px;
  margin: 10px 0px 10px;
}

.con2 p {
  margin-bottom: 0px;
}
.con2 p:nth-of-type(1) {
  font-size: 14px;
}

/* detail */

.detail-wrap {
  width: 90%;
  margin: 100px auto;
  display: flex;
  justify-content: space-between;
}

.detail-wrap img {
  width: 50%;
}

.detail-text {
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.detail-text > h2 {
  font-size: 23px;
  white-space: nowrap;
  margin-bottom: 10px;
}
.detail-text > h4 {
  font-weight: normal;
  font-size: 16px;
  margin: 20px 0px;
}
.detail-text > p {
  font-size: 20px;
  /* margin: 20px 0px; */
}

.btn {
  margin-top: 40px;
  display: inline-block;
  cursor: pointer;
  background: #111;
  color: #fff;
  padding: 10px 30px;
}

.btn:hover {
  border: 1px solid #111;
}

.tab {
  width: 80%;
  margin: 0px auto;
}

.tab-inner {
  height: 200px;
}

/* 순번 리스트 */
.number-list {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.number-list li {
  font-size: 18px;
  cursor: pointer;
}

.number-list li:hover {
  text-decoration: underline;
}

/* footer */
.footer {
  display: flex;
  justify-content: space-between;
  align-items: end;
  padding: 50px;
  border-top: 1px solid #111;
  margin-top: 200px;
}

.footer-left {
  text-align: left;
}

.footer-left p {
  margin: 0px;
}

.footer-right {
  text-align: right;
}

.footer-right p {
  margin: 0px;
}
